    initCanvas () {
      // let percent = 75
      console.log('初始化canvas')
      let canvas = document.getElementById('time-demoA')
      let ctx = canvas.getContext('2d')
      let canvas2 = document.getElementById('time-demoB')
      let ctx2 = canvas2.getContext('2d')
      this.drawHoursScale(ctx, canvas.width / 2, canvas.height / 2, 20, 6, 65, 56)
      if (this.persent < 0 || this.persent > 100) {
        throw new Error('persent must be between 0 and 100')
      };
      let angles = 0
      let timer;
      let that = this;
      (function draw () {
         // console.log(that.persent)
        timer = requestAnimationFrame(draw)

        // 百分比圆环

        let angel = -90 + angles * (360 / 20) // 角度
        let [x1, y1] = [canvas2.width / 2 + Math.cos(angel * Math.PI / 180) * 65, canvas2.height / 2 + Math.sin(angel * Math.PI / 180) * 65]
        let [x2, y2] = [canvas2.width / 2 + Math.cos(angel * Math.PI / 180) * 56, canvas2.height / 2 + Math.sin(angel * Math.PI / 180) * 56]
        ctx2.save()

        angles++
        let percentAge = parseInt((angles / 20) * 100) // 解析字符串并取整
        if (angles > (that.persent / 100 * 20)) {
          percentAge = that.persent
          window.cancelAnimationFrame(timer)
        };
        ctx2.beginPath()
        ctx2.lineWidth = 6
        ctx2.strokeStyle = '#04CF99'
        // ctx2.strokeStyle = '#1D2C3E'
        ctx2.lineCap = 'round'
        ctx2.moveTo(x1, y1)
        ctx2.lineTo(x2, y2)
        ctx2.stroke()
      })()
      // ctx.lineWidth = 15
      // ctx.strokeStyle = '#ccc'
      // ctx.beginPath()
      // ctx.lineCap = 'Square'
      // ctx.arc(canvas.width / 2, canvas.height / 2, canvas.width / 2 - ctx.lineWidth / 2, 0, Math.PI, false)

      // ctx.stroke()
