    drawHoursScale (ctx1, x0, y0, scaleNum, scaleW, maxL, minL, ctx2) {
      for (let i = 0; i < scaleNum; i++) {
        let angel = -90 + i * (360 / scaleNum) // 角度
        let [x1, y1] = [x0 + Math.cos(angel * Math.PI / 180) * maxL, y0 + Math.sin(angel * Math.PI / 180) * maxL]
        let [x2, y2] = [x0 + Math.cos(angel * Math.PI / 180) * minL, y0 + Math.sin(angel * Math.PI / 180) * minL]
        ctx1.save()
        console.log(1)
        ctx1.beginPath()
        ctx1.lineWidth = scaleW
        // ctx.strokeStyle = '#04CF99'
        ctx1.strokeStyle = '#1D2C3E'
        ctx1.lineCap = 'round'
        ctx1.moveTo(x1, y1)
        ctx1.lineTo(x2, y2)
        ctx1.stroke()
        // ctx1.closePath()
