<template>
  <div id="app" class="">
    <div @click="elemnt" id="main" ref="numberGrow" :data-yname="yname" :data-name="name"></div>
    <canvas class="zhejiang" v-for="(item,index) in citys" :id="'canvas'+index"></canvas>
  </div>


</template>

<script>
  // import zhejiang from './maps/zhejiang.json'
  // import hangzhou from './maps/hangzhou.json'
  // import huzhou from './maps/huzhou.json'
  // import jiaxin from './maps/jiaxin.json'
  // import jinhua from './maps/jinhua.json'
  // import lishui from './maps/lishui.json'
  // import ningbo from './maps/ningbo.json'
  // import quzhou from './maps/quzhou.json'
  // import shaoxin from './maps/shaoxin.json'
  // import taizhou from './maps/taizhou.json'
  // import wenzhou from './maps/wenzhou.json'
  // import zhoushan from './maps/zhoushan.json'
  export default {

    name: 'test',
    data() {
      var jsonMap = {
        // '浙江': zhejiang,
        // '杭州市': hangzhou,
        // '湖州市': huzhou,
        // '嘉兴市': jiaxin,
        // '丽水市': lishui,
        // '宁波市': ningbo,
        // '衢州市': quzhou,
        // '绍兴市': shaoxin,
        // '台州市': taizhou,
        // '温州市': wenzhou,
        // '舟山市': zhoushan
      }
      //循环遍历注册地图
      for (let index in jsonMap) {
        this.$echarts.registerMap(index, jsonMap[index])
      };
      return {
        geoJsonData: '',
        geoCoordMap: [],
        datas: [],
        aaa: [],
        citys:[]
      }
    },
    props: {
      yname: {
        type: null,
        default: 'zhejiang'
      },
      name: {
        type: null,
        default: "浙江"
      }
    },
    watch: {
      name(val) {

      }
    },
    methods: {
      aloade(e) {
        let that = this;


        let city = []
        let geoCoordMap = {
          "舟山": [122.207216, 29.985295],
          "宁波": [121.56, 29.86],
          "丽水": [119.92, 28.45],
          "衢州": [118.88, 28.97]
        };

        var convertData = function(data) {
          var res = [];
          for (var i = 0; i < data.length; i++) {
            var geoCoord = geoCoordMap[data[i].name];
            if (geoCoord) {
              res.push({
                name: data[i].name,
                value: geoCoord.concat(data[i].value)
              });
            }
          }
          console.log(res)
          return res;
        };
        let min = 0
        let max = 1500
        this.charts = this.$echarts.init(document.getElementById("main"))

        this.charts.setOption({
          toolbox: {
            show: true,
            left: 20,
            feature: {
              dataView: {
                show: false,
                readOnly: false
              },
              magicType: {
                show: false,
                type: ['line', 'bar']
              },
              restore: {
                show: false
              },
              myTool: {
                show: true,
                title: '返回',
                icon: 'image://http://echarts.baidu.com/images/favicon.png',
                onclick: function() {

                  that.charts.dispose()
                  that.aloade("浙江")
                }
              },
              saveAsImage: {
                show: false
              }
            }
          },

          backgroundColor: '#fff',
          visualMap: {
            show: false,
            min: min,
            max: max,
            inRange: {
              color: ['#e0ffff', '#006edd']
            },
            calculable: true


          },

          series: [{
            name: '浙江',
            type: 'map3D',
            map: e,
            data: city,

            // 环境贴图，支持純颜色值，渐变色，全景贴图的 url。默认为 'auto'，在配置有 light.ambientCubemap.texture 的时候会使用该纹理作为环境贴图。否则则不显示环境贴图。
            environment: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{ // 配置为垂直渐变的背景
              offset: 0,
              color: '#012131' // 天空颜色
            }, {
              offset: 0.8,
              color: '#6f6e6c' // 地面颜色
            }, {
              offset: 1,
              color: '#000000' // 地面颜色
            }], false),

            regionHeight: 3,
            boxWidth: 100,
            //boxHeight:50,
            boxDepth: 85,
            top: '-15%',
            left: '5%',
            shading: 'lambert', //光照带来的明暗
            light: { // 光照相关的设置。在 shading 为 'color' 的时候无效。
              main: { //场景主光源的设置
                intensity: 5, //主光源的强度
                shadow: true, //主光源是否投射阴影
                shadowQuality: 'high', //阴影的质量
                alpha: 30, //主光源绕 x 轴偏离的角度
                beta: 190 //主光源绕 y 轴偏离的角度
              },
              ambient: { //全局的环境光设置。
                intensity: 0 //环境光的强度
              }
            },
            label: {
              show: true,
              formatter: function(params) {
                var content = '',
                  content = params.name;
                return content;
              },
              textStyle: {
                color: 'white',
                fontWeight: 'normal',
                fontSize: 18,
                backgroundColor: 'rgba(0,23,11,0)'
              },

              emphasis: { //对应的鼠标悬浮效果

                show: true,
                textStyle: {
                  color: "red"
                }
              }
            },
            temporalSuperSampling: { //分帧超采样。在开启 postEffect 后，WebGL 默认的 MSAA 会无法使用,分帧超采样用来解决锯齿的问题
              enable: true
            },
            itemStyle: { //三维图形的视觉属性
              color: '#2355ac',
              borderWidth: 1,
              borderColor: '#000'
            },
            // itemStyle: {


            //   normal: {
            //     color: 'rgba(6, 148, 239, 0.9)', // 地图板块的颜色
            //     opacity: 0.9, // 图形的不透明度 [ default: 1 ]
            //     borderWidth: 0.5
            //   }, //阴影效果
            //   emphasis: {
            //     color: 'rgba(6, 207, 239, 0.95)'
            //   }
            // },
            postEffect: { //为画面添加高光，景深，环境光遮蔽（SSAO），调色等效果
              enable: true, //是否开启
              SSAO: { //环境光遮蔽
                radius: 1, //环境光遮蔽的采样半径。半径越大效果越自然
                intensity: 1, //环境光遮蔽的强度
                enable: true
              }
            },

            viewControl: {
              rotateSensitivity: 0,
              // projection: 'orthographic',
              orthographicSize: 30, //控制地图大小
              maxOrthographicSize: 30,
              minOrthographicSize: 30,
              autoRotate: false,
              animation: true,

              // autoRotateSpeed:5
            }


          }, ]
        })

        this.charts.on('click', function(params) {
          that.citys = []
          var cout = params.data.name;
          this.name = params.data.name;
          console.log(that.$store.state.TallCityName)
          console.log(that.$store.state.CityName)
          that.$store.dispatch("changeLTFuc", that.$store.state.CityName)
          that.$store.dispatch("changeFuc", params.data.name)
          that.$store.dispatch("changeLFuc", 2)
          console.log(that.$store.state.TallCityName)
          console.log(that.$store.state.CityName)

          // 行政区查询
          // 按照adcode进行查询可以保证数据返回的唯一性
          this.district = new AMap.DistrictSearch(this.opts)
          this.district.search(params.data.name, (status, result) => {

            if (status === 'complete') {

              that.getData(result.districtList[0], params.data.name, result.districtList[0].adcode) // 这个getData函数在前面已经定义过了
            }

            that.elemnt(params.data.name)


          })
        })
      },
      elemnt(e) {


        this.$emit('active', e)

      },
      getData(data, level, adcode) { // 处理获取出来的边界数据

        var subList = data.districtList
        if (subList) {
          var curlevel = subList[0].level
          if (curlevel === 'street') { // 为了配合echarts地图区县名称显示正常，这边街道级别数据需要特殊处理

            let mapJsonList = this.geoJsonData.features
            let mapJson = {}
            for (let i in mapJsonList) {
              if (mapJsonList[i].properties.name == level) {
                mapJson.features = [].concat(mapJsonList[i])
              }
            }
            this.mapData = []
            // 这个mapData里包含每个区域的code、名称、对应的等级，实现第三步功能时能用上
            this.mapData.push({
              name: this.cityName,
              value: Math.random() * 100,
              level: curlevel
            })

            this.$echarts.registerMap(this.$store.state.CityName, mapJson)
            if (this.$store.state.CityLevel != 1) {
              this.charts.dispose()
            }
            this.aloade(this.$store.state.CityName)
            this.geoJsonData = mapJson
            return
          }

          // 街道级以上的数据处理方式
          this.mapData = []
          for (var i = 0, l = subList.length; i < l; i++) {
            var name = subList[i].name
            var cityCode = subList[i].adcode
            var curlevel = subList[i].level
            // 这个mapData里包含每个区域的code、名称、对应的等级，实现第三步功能时能用上
            this.mapData.push({
              name: name,
              value: Math.random() * 100,
              cityCode: cityCode,
              level: curlevel
            })
          }
          this.loadMapData(adcode)
        }
      },
      loadMapData(areaCode) {
        AMapUI.loadUI(['geo/DistrictExplorer'], DistrictExplorer => {
          // 创建一个实例
          var districtExplorer = window.districtExplorer = new DistrictExplorer({
            eventSupport: true, // 打开事件支持
            map: this.map
          })

          districtExplorer.loadAreaNode(areaCode, (error, areaNode) => {
            if (error) {
              console.error(error)
              return
            }
            let mapJson = {}
            // 特别注意这里哦，如果查看过正常的geojson文件，都会发现，文件都是以features 字段开头的，所以这里要记得加上
            mapJson.features = areaNode.getSubFeatures()

            this.geoJsonData = mapJson;
            this.$echarts.registerMap(this.$store.state.CityName, mapJson)

            if (this.$store.state.CityLevel != 1) {
              this.charts.dispose()
            }

            this.aloade(this.$store.state.CityName)
            // this.aloade("浙江")
            // this.loadMap(this.cityName, mapJson)
          })
        })
      },
      kuosan(id){
        var canvas = document.getElementById(id);
             var ctx = canvas.getContext('2d');

             canvas.width = 40;
             canvas.height = 40;

             var radius = 0;

             function drawCircle() {
                 ctx.beginPath();
                 ctx.arc(20, 20, radius, 0, Math.PI * 2);
                 ctx.closePath();
                 ctx.lineWidth = 2;
                 ctx.strokeStyle = 'rgba(255,50,50,1)';
                 ctx.stroke();

                 radius += 0.5;
                 if (radius > 18) {
                     radius = 0;
                 }
             }

             function render() {
                 var prev = ctx.globalCompositeOperation;
                 ctx.globalCompositeOperation = 'destination-in';
                 ctx.globalAlpha = 0.95;
                 ctx.fillRect(0, 0, canvas.width, canvas.height);
                 ctx.globalCompositeOperation = prev;
                 drawCircle();
             };
             var testtimer = setInterval(function() {
                 render();
             }, 35);
      }
    },
    created() {

    },
    mounted() {
      // this.echartsMap = this.$echarts.init(document.getElementById('map'))
      this.opts = {
        subdistrict: 1, // 返回下一级行政区
        showbiz: false // 最后一级返回街道信息
      }
      let that = this
      this.district = new AMap.DistrictSearch(this.opts) // 注意：需要使用插件同步下发功能才能这样直接使用
      this.district.search('浙江', (status, result) => {
        console.log(result)
        this.citys = this.citys.concat(result.districtList[0].districtList)
                console.log(this.citys)
        setTimeout(function(){
          for(let i = 0; i < result.districtList[0].districtList.length; i++){
            var id = 'canvas'+i
            that.kuosan(id)
          }

        },100)

        for (let i = 0; i < result.districtList[0].districtList.length; i++) {


          this.geoCoordMap.push(result.districtList[0].districtList[i].name)
          let data = [] = [result.districtList[0].districtList[i].center.lat, result.districtList[0].districtList[i]
            .center.lng
          ]

          this.datas.push(data)

          this.aaa.push({
            name: result.districtList[0].districtList[i].name,
            value: this.datas[i]
          });
          console.log(this.aaa)
        }

        if (status == 'complete') {
          this.getData(result.districtList[0], 'province', 330000)
        }
      })




    }
  }
</script>
<style>
  #main {
    /* border: 2px solid green; */
    margin: 0 auto;
    width: 500px;
    height: 400px;
    /* background-image: url(../../../assets/img/bg.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; */
  }
  #app{
    position: relative;
  }
  #canvas0{

    left: 40%;
    top: 45%;
  }
  #canvas1{

    left: 50%;
    top: 55%;
  }
  #canvas2{

    left: 30%;
    top: 30%;
  }
  #canvas3{

    left: 45%;
    top: 28%;
  }
  #canvas4{

    left: 42%;
    top: 15%;
  }
  #canvas5{

    left: 45%;
    top: 8%;
  }
  #canvas6{

    left: 54%;
    top: 10%;
  }
  #canvas7{

    left: 52%;
    top: 18%;
  }
  #canvas8{

    left: 60%;
    top: 18%;
  }
  #canvas9{

    left: 68%;
    top: 15%;
  }
  #canvas10{

    left: 60%;
    top: 32%;
  }

  .zhejiang{
     position: absolute;
     transform: rotateX(50deg);
     -ms-transform:rotateX(50deg)); /* IE 9 */
         -webkit-transform:rotateX(50deg); /* Safari and Chrome */
  }
</style>



