<template>
  <div id="container" style="height: 1000px; width: 1000px;"></div>
</template>

<script>
import AMap from 'AMap'
  import AMapUI from 'AMapUI'
  export default {

    name: 'HelloWorld',
    data() {
      return {
        geoJsonData: '',
        dataa: '',
        dataA: '',
        city: ""
      }
    },
    mounted() {
      let that = this

      new AMap.DistrictSearch({
        subdistrict: 1, // 返回下一级行政区
        extensions: 'all', // 返回行政区边界坐标组等具体信息
        level: 'district' // 查询行政级别为 市
      }).search(this.$store.state.City, function(status, result) {
        that.city = result.districtList[0].districtList;
        let lat = result.districtList[0].center.lat
        let lng = result.districtList[0].center.lng
        console.log(result)
        let map = new AMap.Map('container', {
          viewMode: '3D',
          mapStyle: 'amap://styles/dark', //设置地图的显示样式
          showLabel: false, //不显示地图文字标记
          pitch: 45,
          zoom: 11,
          center: [lng, lat],
          resizeEnable: true
        })


        // 设置光照
        map.AmbientLight = new AMap.Lights.AmbientLight([1, 1, 1], 0.5)
        map.DirectionLight = new AMap.Lights.DirectionLight([0, 0, 1], [1, 1, 1], 1)

        let object3Dlayer = new AMap.Object3DLayer({
          zIndex: 1
        });
        map.add(object3Dlayer)

        for (let i = 0; i < that.city.length; i++) {
          new AMap.DistrictSearch({
            subdistrict: 1, // 返回下一级行政区
            extensions: 'all', // 返回行政区边界坐标组等具体信息
            level: 'district' // 查询行政级别为 市
          }).search(that.city[i].adcode, function(status, result) {

            that.city[i].value = result.districtList[0].districtList
            let bounds = result.districtList[0].boundaries
            that.dataa = result.districtList[0].boundaries
            that.dataA = result.districtList[0]
            let height = 20000
            let color = '#0088ffe6' // rgba
            // let color = that.city[i].color // rgba
            let prism = 'prism' + i

            map.add(object3Dlayer)

            prism = new AMap.Object3D.Prism({
              path: bounds,
              height: height,
              color: color
            })

            prism.transparent = true
            object3Dlayer.add(prism)
            var wall = new AMap.Object3D.Wall({
              path: bounds,
              height: height,
              color: "white"
            });
            wall.transparent = true
            object3Dlayer.add(wall)
            //添加描边
            for (var j = 0; j < bounds.length; j += 1) {
              new AMap.Polyline({
                path: bounds[j],
                strokeColor: '#fff', //边线颜色
                strokeWeight: 1,
                map: map
              })
            }



            let posi = []

            posi.push(that.dataA.center.lng)
            posi.push(that.dataA.center.lat)
            // /*
            //  * 添加Canvas图层
            //  */
            // var canvas = document.createElement('canvas');
            // canvas.width = 200;
            // canvas.height = 200;

            // var radius = 0;

            // var ctx = canvas.getContext('2d')

            // function drawCircle() {
            //   ctx.beginPath();
            //   ctx.arc(100, 100, radius, 0, Math.PI * 2);
            //   ctx.closePath();
            //   ctx.lineWidth = 2;
            //   ctx.strokeStyle = 'rgba(255,50,50,1)';
            //   ctx.stroke();

            //   radius += 0.5;
            //   if (radius > 30) {
            //     radius = 0;
            //   }
            // }
            // var draw = function() {
            //   var prev = ctx.globalCompositeOperation;
            //   ctx.globalCompositeOperation = 'destination-in';
            //   ctx.globalAlpha = 0.95;
            //   ctx.fillRect(0, 0, canvas.width, canvas.height);
            //   ctx.globalCompositeOperation = prev;
            //   drawCircle();

            //   //2D视图时可以省略
            //   CanvasLayer.reFresh();

            //   AMap.Util.requestAnimFrame(draw);
            // };

            let text = new AMap.Text({
              text: result.districtList[0].name,
              verticalAlign: 'bottom',
              position: posi,
              height: 5000,
              style: {
                'background-color': 'transparent',
                '-webkit-text-stroke': 'red',
                '-webkit-text-stroke-width': '0.5px',
                'text-align': 'center',
                'border': 'none',
                'color': 'white',
                'font-size': '24px',
                'font-weight': 600
              }
            })
            // var CanvasLayer = new AMap.CanvasLayer({

            //   canvas: canvas,
            //   bounds: new AMap.Bounds(
            //     [that.dataA.center.lng - 0.3, that.dataA.center.lat - 0.3],
            //     [that.dataA.center.lng + 0.3, that.dataA.center.lat + 0.3]
            //   ),
            //   zooms: [3, 18],
            //   zIndex: 30,
            //   height: 6000,
            // });

            // CanvasLayer.setMap(map);


            text.setMap(map)
            // map.destroy()
            // 点标记显示内容，HTML要素字符串
            if(that.city[i].name == "杭州市"){
            var markerContent = '' +
              '<div class="custom-content-marker">' +

              '   <div class="area-box" style="z-index: 77; position: relative;  height: 88px;width: 88px;"><span class="dot"></span><span class="pulse pulse-red delay-06"></span><span class="pulse pulse-red delay-05"></span><span class="pulse pulse-red delay-04"></span></div>' +
              '</div>';
            }else if(that.city[i].name == "嘉兴市"){
              var markerContent = '' +
                '<div class="custom-content-marker">' +

                '   <div class="area-box" style="z-index: 77; position: relative;  height: 88px;width: 88px;"><span class="dot"></span><span class="pulse pulse-gold delay-06"></span><span class="pulse pulse-gold delay-05"></span><span class="pulse pulse-gold delay-04"></span></div>' +
                '</div>';
            }else{
              var markerContent = '' +
                '<div class="custom-content-marker">' +

                '   <div class="area-box" style="z-index: 77; position: relative;  height: 88px;width: 88px;"><span class="dot"></span><span class="pulse pulse-green delay-06"></span><span class="pulse pulse-green delay-05"></span><span class="pulse pulse-green delay-04"></span></div>' +
                '</div>';
            }


            var marker = new AMap.Marker({
              position: posi,
              // 将 html 传给 content
              content: markerContent,
              // // 以 icon 的 [center bottom] 为原点
               offset: new AMap.Pixel(-5, -5)
            });

            // 将 markers 添加到地图
            map.add(marker);
          })
        }
        map.on('click', function(ev) {


          let geocoder = new AMap.Geocoder({
            city: "", //城市设为北京，默认：“全国”
            radius: 1000 //范围，默认：500
          });
          let lnglatXY = [ev.lnglat.lng, ev.lnglat.lat]; //地图上所标点的坐标


          let pixel = ev.pixel
          let px = new AMap.Pixel(pixel.x, pixel.y)
          let obj = map.getObject3DByContainerPos(px, [object3Dlayer], false) || {}
          // 选中的 object3D 对象，这里为当前 Mesh
          let object = obj.object
          // console.log(object.C.color)

          if (object.id) {
            geocoder.getAddress(lnglatXY, function(status, result) {
              if (status === 'complete' && result.info === 'OK') {
                //获得了有效的地址信息:
                //即，result.regeocode.formattedAddress

                let city = result.regeocode.addressComponent.district;

             that.$store.dispatch("changeAFuc", city)
             that.$router.push({
               name: 'Area',
               params: {
                 // lotterydata: lotterydata
               }
             })
              } else {
                let city = '获取失败';
                //获取地址失败
              }


            });

          } else {
            console.log(2)
          }
        })
         });


    },
    methods: {
      colors() {
        let r = Math.floor(Math.random() * 256);
        let g = Math.floor(Math.random() * 256);
        let b = Math.floor(Math.random() * 256);
        return "rgb(" + r + ',' + g + ',' + b + ")";
      },
      showInfoOut() {
        console.log(123)
      },
    }
  }
</script>

<style>
</style>



