<template>
  <div id="container" style="height: 1000px; width: 1000px;"></div>
</template>

<script>
  import AMap from 'AMap'
  import AMapUI from 'AMapUI'
  export default {

    name: 'HelloWorld',
    data() {
      return {
        geoJsonData: '',
        dataa: '',
        dataA: '',
        city: [{
          name: '杭州市',
          value: [123, 'idc'],
          color: '#0088ffcc'
        }, {
          name: '宁波市',
          value: [123, 'idc'],
          color: 'rgba(202, 123, 220, 0.7)'
        }, {
          name: '温州市',
          value: [123, 'idc'],
          color: 'rgba(6, 123, 22, 0.7)'
        }, {
          name: '绍兴市',
          value: [123, 'idc'],
          color: 'rgba(182, 63, 22, 0.7)'
        }, {
          name: '金华市',
          value: [123, 'idc'],
          color: 'rgba(252, 3, 22, 0.7)'
        }, {
          name: '台州市',
          value: [123, 'idc'],
          color: 'rgba(72, 123, 22, 0.7)'
        }, {
          name: '衢州市',
          value: [123, 'idc'],
          color: 'rgba(122, 123, 22, 0.7)'
        }, {
          name: '湖州市',
          value: [123, 'idc'],
          color: 'rgba(2, 3, 220, 0.7)'
        }, {
          name: '嘉兴市',
          value: [123, 'idc'],
          color: 'rgba(25, 223, 222, 0.7)'
        }, {
          name: '舟山市',
          value: [123, 'idc'],
          color: 'rgba(22, 123, 22, 0.7)'
        }, {
          name: '丽水市',
          value: [123, 'idc'],
          color: 'rgba(12, 123, 22, 0.8)'
        }]
      }
    },
    mounted() {
      let that = this
      let map = new AMap.Map('container', {
        viewMode: '3D',
        mapStyle: 'amap://styles/dark', //设置地图的显示样式
        showLabel: false, //不显示地图文字标记
        pitch: 45,
        zoom: 8,
        center: [120.480766, 27.7],
        resizeEnable: true
      })


      // 设置光照
      map.AmbientLight = new AMap.Lights.AmbientLight([1, 1, 1], 0.5)
      map.DirectionLight = new AMap.Lights.DirectionLight([0, 0, 1], [1, 1, 1], 1)

      let object3Dlayer = new AMap.Object3DLayer({
        zIndex: 1
      });
      map.add(object3Dlayer)

      for (let i = 0; i < that.city.length; i++) {
        new AMap.DistrictSearch({
          subdistrict: 1, // 返回下一级行政区
          extensions: 'all', // 返回行政区边界坐标组等具体信息
          level: 'city' // 查询行政级别为 市
        }).search(that.city[i].name, function(status, result) {

          that.city[i].value = result.districtList[0].districtList
          let bounds = result.districtList[0].boundaries
          that.dataa = result.districtList[0].boundaries
          that.dataA = result.districtList[0]
          let height = 20000
          let color = '#0088ffe6' // rgba
          // let color = that.city[i].color // rgba
          let prism = 'prism' + i

          map.add(object3Dlayer)

          prism = new AMap.Object3D.Prism({
            path: bounds,
            height: height,
            color: color
          })

          prism.transparent = true
          object3Dlayer.add(prism)
          var wall = new AMap.Object3D.Wall({
            path: bounds,
            height: height,
            color: "white"
          });
          wall.transparent = true
          object3Dlayer.add(wall)
          //添加描边
          for (var j = 0; j < bounds.length; j += 1) {
            new AMap.Polyline({
              path: bounds[j],
              strokeColor: '#fff', //边线颜色
              strokeWeight: 1,
              map: map
            })
          }



          let posi = []

          posi.push(that.dataA.center.lng)
          posi.push(that.dataA.center.lat)
          /*
           * 添加Canvas图层
           */
          var canvas = document.createElement('canvas');
          canvas.width = 40;
          canvas.height = 40;

          var radius = 0;

          var ctx = canvas.getContext('2d')

          function drawCircle() {
            ctx.beginPath();
            ctx.arc(20, 20, radius, 0, Math.PI * 2);
            ctx.closePath();
            ctx.lineWidth = 2;
            ctx.strokeStyle = 'rgba(255,50,50,1)';
            ctx.stroke();

            radius += 0.5;
            if (radius > 18) {
              radius = 0;
            }
          }
          var draw = function() {
            var prev = ctx.globalCompositeOperation;
            ctx.globalCompositeOperation = 'destination-in';
            ctx.globalAlpha = 0.95;
            ctx.fillRect(0, 0, canvas.width, canvas.height);
            ctx.globalCompositeOperation = prev;
            drawCircle();

            //2D视图时可以省略
            CanvasLayer.reFresh();

            AMap.Util.requestAnimFrame(draw);
          };

          let text = new AMap.Text({
            text: result.districtList[0].name,
            verticalAlign: 'bottom',
            position: posi,
            height: 5000,
            style: {
              'background-color': 'transparent',
              '-webkit-text-stroke': 'red',
              '-webkit-text-stroke-width': '0.5px',
              'text-align': 'center',
              'border': 'none',
              'color': 'white',
              'font-size': '24px',
              'font-weight': 600
            }
          })
          var CanvasLayer = new AMap.CanvasLayer({

            canvas: canvas,
            bounds: new AMap.Bounds(
              [that.dataA.center.lng - 0.3, that.dataA.center.lat - 0.3],
              [that.dataA.center.lng + 0.3, that.dataA.center.lat + 0.3]
            ),
            zooms: [3, 18],
            zIndex: 30,
            height: 6000,
          });

          // CanvasLayer.setMap(map);

          text.setMap(map)
          draw();
          // map.destroy()
          // 点标记显示内容，HTML要素字符串
          if(that.city[i].name == "杭州市"){
          var markerContent = '' +
            '<div class="custom-content-marker">' +

            '   <div class="area-box" style="z-index: 77; position: relative;  height: 88px;width: 88px;"><span class="dot"></span><span class="pulse pulse-red delay-06"></span><span class="pulse pulse-red delay-05"></span><span class="pulse pulse-red delay-04"></span></div>' +
            '</div>';
          }else if(that.city[i].name == "嘉兴市"){
            var markerContent = '' +
              '<div class="custom-content-marker">' +

              '   <div class="area-box" style="z-index: 77; position: relative;  height: 88px;width: 88px;"><span class="dot"></span><span class="pulse pulse-gold delay-06"></span><span class="pulse pulse-gold delay-05"></span><span class="pulse pulse-gold delay-04"></span></div>' +
              '</div>';
          }else{
            var markerContent = '' +
              '<div class="custom-content-marker">' +

              '   <div class="area-box" style="z-index: 77; position: relative;  height: 88px;width: 88px;"><span class="dot"></span><span class="pulse pulse-green delay-06"></span><span class="pulse pulse-green delay-05"></span><span class="pulse pulse-green delay-04"></span></div>' +
              '</div>';
          }


          var marker = new AMap.Marker({
            position: posi,
            // 将 html 传给 content
            content: markerContent,
            // // 以 icon 的 [center bottom] 为原点
             offset: new AMap.Pixel(-5, -5)
          });

          // 将 markers 添加到地图
          map.add(marker);
        })
      }
      map.on('click', function(ev) {


        let geocoder = new AMap.Geocoder({
          city: "", //城市设为北京，默认：“全国”
          radius: 1000 //范围，默认：500
        });
        let lnglatXY = [ev.lnglat.lng, ev.lnglat.lat]; //地图上所标点的坐标


        let pixel = ev.pixel
        let px = new AMap.Pixel(pixel.x, pixel.y)
        let obj = map.getObject3DByContainerPos(px, [object3Dlayer], false) || {}
        // 选中的 object3D 对象，这里为当前 Mesh
        let object = obj.object
        // console.log(object.C.color)

        if (object.id) {
          geocoder.getAddress(lnglatXY, function(status, result) {
            if (status === 'complete' && result.info === 'OK') {
              //获得了有效的地址信息:
              //即，result.regeocode.formattedAddress

              let city = result.regeocode.addressComponent.city;
              console.log(city)
              that.$store.dispatch("changeCFuc", city)
              that.$router.push({
                name: 'City',
                params: {
                  // lotterydata: lotterydata
                }
              })
              //  new AMap.DistrictSearch({
              //    subdistrict: 1, // 返回下一级行政区
              //    extensions: 'all', // 返回行政区边界坐标组等具体信息
              //    level: 'city' // 查询行政级别为 市
              //  }).search(city, function(status, result) {
              // console.log(result)

              // // that.drawmap(result.districtList[0].districtList,result.districtList[0].center.lat,result.districtList[0].center.lng)
              //  });
            } else {
              let city = '获取失败';
              //获取地址失败
            }


          });

        } else {

        }
      })
    },
    methods: {
      colors() {
        let r = Math.floor(Math.random() * 256);
        let g = Math.floor(Math.random() * 256);
        let b = Math.floor(Math.random() * 256);
        return "rgb(" + r + ',' + g + ',' + b + ")";
      },
      showInfoOut() {
        console.log(123)
      },
      drawmap(e, lat, lng) {

        let that = this
        let map = new AMap.Map('container', {
          viewMode: '3D',
          mapStyle: 'amap://styles/dark', //设置地图的显示样式
          showLabel: false, //不显示地图文字标记
          pitch: 45,
          zoom: 8,
          center: [lng, lat]
        })
        // 设置光照
        map.AmbientLight = new AMap.Lights.AmbientLight([1, 1, 1], 0.5)
        map.DirectionLight = new AMap.Lights.DirectionLight([0, 0, 1], [1, 1, 1], 1)

        let object3Dlayer = new AMap.Object3DLayer({
          zIndex: 1
        });
        map.add(object3Dlayer)
        for (let i = 0; i < e.length; i++) {
          new AMap.DistrictSearch({
            subdistrict: 0, // 返回下一级行政区
            extensions: 'all', // 返回行政区边界坐标组等具体信息
            level: 'city' // 查询行政级别为 市
          }).search(e[i].name, function(status, result) {

            let bounds = result.districtList[0].boundaries

            that.dataA = result.districtList[0]
            let height = 5000 + i * 1000
            // let color = that.colors() // rgba
            let color = '#0088ffcc'

            let prism = new AMap.Object3D.Prism({
              path: bounds,
              height: height,
              color: color
            })

            prism.transparent = true
            object3Dlayer.add(prism)
            var wall = new AMap.Object3D.Wall({
              path: bounds,
              height: height,
              color: "white"
            });
            wall.transparent = true
            object3Dlayer.add(wall)
            //添加描边
            for (var j = 0; j < bounds.length; j += 1) {
              new AMap.Polyline({
                path: bounds[j],
                strokeColor: '#fff', //边线颜色
                strokeWeight: 1,
                map: map
              })
            }

            let posi = []

            posi.push(that.dataA.center.lng)
            posi.push(that.dataA.center.lat)
            /*
             * 添加Canvas图层
             */
            var canvas1 = document.createElement('canvas');
            canvas1.width = 20;
            canvas1.height = 20;

            var radius = 0;

            var ctx = canvas1.getContext('2d')

            function drawCircle() {
              ctx.beginPath();
              ctx.arc(10, 10, radius, 0, Math.PI * 2);
              ctx.closePath();
              ctx.lineWidth = 2;
              ctx.strokeStyle = 'rgba(255,50,50,1)';
              ctx.stroke();

              radius += 0.5;
              if (radius > 9) {
                radius = 0;
              }
            }
            var draw = function() {
              var prev = ctx.globalCompositeOperation;
              ctx.globalCompositeOperation = 'destination-in';
              ctx.globalAlpha = 0.95;
              ctx.fillRect(0, 0, canvas1.width, canvas.height);
              ctx.globalCompositeOperation = prev;
              drawCircle();

              //2D视图时可以省略
              CanvasLayer.reFresh();

              AMap.Util.requestAnimFrame(draw);
            };
            let text = new AMap.Text({
              text: result.districtList[0].name,
              verticalAlign: 'bottom',
              position: posi,
              height: 5000,
              style: {
                'background-color': 'transparent',
                '-webkit-text-stroke': 'red',
                '-webkit-text-stroke-width': '0.5px',
                'text-align': 'center',
                'border': 'none',
                'color': 'white',
                'font-size': '24px',
                'font-weight': 600
              }
            })
            var CanvasLayer = new AMap.CanvasLayer({

              canvas: canvas1,
              bounds: new AMap.Bounds(
                [that.dataA.center.lng - 0.3, that.dataA.center.lat - 0.3],
                [that.dataA.center.lng + 0.3, that.dataA.center.lat + 0.3]
              ),
              zooms: [3, 18],
              zIndex: 30,
              height: 6000,
            });

            CanvasLayer.setMap(map);

            text.setMap(map)
            draw();
            // map.destroy()
          })
        }
        map.on('click', function(ev) {
          // let bounds = that.dataa
          // let height = 50000
          // let color = 'rgba(202, 123, 22, 0.7)' // rgba
          // let prism1 = new AMap.Object3D.Prism({
          //   path: bounds,
          //   height: height,
          //   color: color
          // })

          // prism1.transparent = true
          // //  object3Dlayer.add(prism)
          let pixel = ev.pixel
          let px = new AMap.Pixel(pixel.x, pixel.y)
          let obj = map.getObject3DByContainerPos(px, [object3Dlayer], false) || {}


          // 选中的 face 所在的索引
          let index = obj.index
          // 选中的 object3D 对象，这里为当前 Mesh
          let object = obj.object
          // 被拾取到的对象和拾取射线的交叉点的3D坐标
          let point = obj.point
          // 交叉点距透视原点的距离
          let distance = obj.distance


          if (object.id) {

            // console.log(that.city[i].color)
            if (object.C.color == '#0088ffcc') {
              console.log("杭州")
            }


            // that.drawmap()
          } else {
            console.log(2)
          }
        })
      }
    }
  }
</script>

<style>

  @keyframes warn {
      0% {
          transform: scale(0);
          opacity: 0.0;
      }
      25% {
          transform: scale(0.1);
          opacity: 0.1;
      }
      50% {
          transform: scale(0.3);
          opacity: 0.3;
      }
      75% {
          transform: scale(0.5);
          opacity: 0.5;
      }
      100% {
          transform: scale(0.75);
          opacity: 0.0;
      }
  }
  @-webkit-keyframes "warn" {
      0% {
          -webkit-transform: scale(0);
          opacity: 0.0;
      }
      25% {
          -webkit-transform: scale(0.1);
          opacity: 0.1;
      }
      50% {
          -webkit-transform: scale(0.3);
          opacity: 0.3;
      }
      75% {
          -webkit-transform: scale(0.5);
          opacity: 0.5;
      }
      100% {
          -webkit-transform: scale(0.75);
          opacity: 0.0;
      }
  }

  #container {
              height: 100%;
              width: 100%;
              }
              .area-box {
                z-index: 77;
                position: relative;
                height: 88px;
                width: 88px;}

                .dot {
                  position: absolute;
                  left: 3px;
                  top: 3px;
                  width: 10px;
                  height: 10px;
                  -webkit-border-radius: 50%;
                  -moz-border-radius: 50%;
                  border-radius: 50%;
                  background: #a2a9b4;
                  opacity: 1;
                  background: #009fd9;
                }

                .pulse {
                  position: absolute;
                  border: 3px solid #b7b7b7;
                  border-radius: 48px;
                  opacity: .12;
                  filter: alpha(opacity=0);
                  -webkit-animation: warn 2s ease-out both;
                  -moz-animation: warn 2s ease-out both;
                  -o-animation: warn 2s ease-out both;
                  animation: warn 2s ease-out both;
                  -webkit-animation-iteration-count: infinite;
                  -moz-animation-iteration-count: infinite;
                  -o-animation-iteration-count: infinite;
                  animation-iteration-count: infinite;
                  background: 0 0;

                  top: -39px;
                  left: -39px;
                  height: 88px;
                  width: 88px;

                }
                .pulse-red{
                  border-color: red;
                  -webkit-box-shadow: 0 0 12px red, 0 0 20px red inset;
                  -moz-box-shadow: 0 0 12px red, 0 0 20px red inset;
                  box-shadow: 0 0 12px red, 0 0 20px red inset;
                }
                .pulse-gold{
                  border-color: gold;
                  -webkit-box-shadow: 0 0 12px gold, 0 0 20px gold inset;
                  -moz-box-shadow: 0 0 12px gold, 0 0 20px gold inset;
                  box-shadow: 0 0 12px gold, 0 0 20px gold inset;
                }
                .pulse-green{
                  border-color: green;
                  -webkit-box-shadow: 0 0 12px green, 0 0 20px green inset;
                  -moz-box-shadow: 0 0 12px green, 0 0 20px green inset;
                  box-shadow: 0 0 12px green, 0 0 20px green inset;
                }
                .area-box .delay-06 {
                    -webkit-animation-delay: 2s;
                    -moz-animation-delay: 2s;
                    -o-animation-delay: 2s;
                    animation-delay: 2s;
                }
                .delay-05 {
                    -webkit-animation-delay: 1.6s;
                    -moz-animation-delay: 1.6s;
                    -o-animation-delay: 1.6s;
                    animation-delay: 1.6s;
                }
                 .delay-04 {
                    -webkit-animation-delay: 1.2s;
                    -moz-animation-delay: 1.2s;
                    -o-animation-delay: 1.2s;
                    animation-delay: 1.2s;
                }

</style>



