import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
let store = new Vuex.Store({
  state: {
    TallCityName: "浙江",
    CityName: "浙江",
    CityLevel:1,
    City:"杭州市",
    Area:"上城区",
  },
  getters: {
    getloding(state) {
      return state.CityName
    }
  },
  actions: {
    changeFuc(name,data) {
      name.commit('change', data)
    },
    changeLFuc(name,data) {
      name.commit('changeL', data)
    },
    changeLTFuc(name,data) {
      name.commit('changeLT', data)
    },
    changeCFuc(name,data) {
      name.commit('changeC', data)
    },
    changeAFuc(name,data) {
      name.commit('changeA', data)
    },
  },
  mutations: {
    change(state,data) {
      state.CityName = data
    },
    changeL(state,data) {
      state.CityLevel = data
    },
    changeLT(state,data) {
      state.TallCityName = data
    },
    changeC(state,data) {
      state.City = data
    },
    changeA(state,data) {
      state.Area = data
    },
  },
})
export default store



