<template>
  <el-container style="background-color: #F6F5FA;">
    <el-header>
      <div class="herder-left">
        <img src="../../assets/logo.png" />
        <!-- <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
          <el-radio-button v-if="isCollapse" :label="false"> <img src="../../assets/logo.png" /></el-radio-button>
          <el-radio-button v-if="!isCollapse" :label="true"> <img src="../../assets/logo.png" /></el-radio-button>
        </el-radio-group> -->

        <span>未来社区-任务分发系统</span>
      </div>
      <div class="herder-right">
        <i @click="newdemo" class="el-icon-search"></i>
        <img :label="false" src="../../assets/logo.png" />
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            管理员名称<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown" style="    margin-top: -10px;">
            <el-dropdown-item icon="el-icon-plus">黄金糕</el-dropdown-item>
            <el-dropdown-item icon="el-icon-circle-plus">狮子头</el-dropdown-item>
            <el-dropdown-item icon="el-icon-circle-plus-outline">螺蛳粉</el-dropdown-item>
            <el-dropdown-item icon="el-icon-check">双皮奶</el-dropdown-item>
            <el-dropdown-item icon="el-icon-circle-check">蚵仔煎</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container style="background-color: #F6F5FA;min-width: 700px;">
      <el-aside width="200px" style="background-color: white;">
        <el-scrollbar>
          <el-menu default-active="3" style="border: none;" active-text-color="#409EFF" class="el-menu-vertical-demo"
            :collapse="isCollapse" @open="handleOpen" @close="handleClose">
            <el-menu-item index="3">
              <i class="el-icon-document"></i>
              <span slot="title" class="danghan-posiA">首页</span>
            </el-menu-item>
            <el-submenu index="1" class="sss">
              <template slot="title" >
                <i class="el-icon-menu"></i>
                <span class="danghan-posi">导航一</span>
              </template>

                <el-menu-item class="aaa" v-for="(item,index) in ['全部','待审核','待处理']" :index='item'><span>{{item}}</span></el-menu-item>

            </el-submenu>
            <el-menu-item index="2">
              <i class="el-icon-document"></i>
              <span slot="title" class="danghan-posiA">导航二</span>
            </el-menu-item>

            <el-menu-item index="4">
              <i class="el-icon-document"></i>
              <span slot="title" class="danghan-posiA">导航四</span>
            </el-menu-item>
          </el-menu>
        </el-scrollbar>
      </el-aside>
      <el-main style="padding-bottom: 0;">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>活动管理</el-breadcrumb-item>
          <el-breadcrumb-item>活动列表</el-breadcrumb-item>
          <el-breadcrumb-item>活动详情</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="checkout">
          <div class="checkout-top">
            <el-form :inline="true" :model="formInline" class="demo-form-inline"  style="margin-bottom: -18px;">
              <el-form-item label="任务名称">
                <el-input v-model="formInline.user" placeholder="请输入名称"></el-input>
              </el-form-item>
              <el-form-item label="任务编号">
                <el-input v-model="formInline.region" placeholder="请输入编号"></el-input>
                <!-- <el-select v-model="formInline.region" placeholder="活动区域">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select> -->
              </el-form-item>
              <el-form-item label="执行人">
                <el-input v-model="formInline.user" placeholder="请输入名称"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button icon="el-icon-search" type="primary" @click="onSubmit">查询</el-button>
                <el-button icon="el-icon-sort" @click="show2 = !show2">筛选</el-button>
              </el-form-item>
            </el-form>

          </div>
          <div class="checkout-bottom" style="margin-left: 10px;   ">
            <transition name="el-zoom-in-top" >
              <el-form v-show="show2" label-position="right" :model="ruleForm" :rules="rules" ref="ruleForm"
                label-width="80px" class="demo-ruleForm" style=" padding-top: 10px;">
                <el-form-item  prop="resource1">
                  <div slot="label" style="color: #333333;">状态:</div>
                  <el-radio-group v-model="ruleForm.resource1">
                    <el-radio label="待接收"></el-radio>
                    <el-radio label="待反馈"></el-radio>
                    <el-radio label="待评价"></el-radio>
                    <el-radio label="已完成"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item  prop="resource2">
                  <div slot="label" style="color: #333333;">任务类型:</div>
                  <el-radio-group v-model="ruleForm.resource2" style="float: left;">
                    <el-radio label="日常任务" style="line-height: 32px;"></el-radio>
                    <el-radio label="临时任务"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item  prop="resource3">
                  <div slot="label" style="color: #333333;">紧急程度:</div>
                  <el-radio-group v-model="ruleForm.resource3" style="    display: flex;">
                    <el-radio label="普通" style="line-height: 32px;"></el-radio>
                    <el-radio label="困难" style="line-height: 32px;"></el-radio>
                    <el-radio label="非常困难" style="line-height: 32px;"></el-radio>
                  </el-radio-group>
                </el-form-item>


                <el-form-item>
                  <el-button @click="resetForm('ruleForm')">重置</el-button>
                  <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
                  <el-button @click="show2 = false" style="border: none;color: blue;" icon="el-icon-arrow-up">收起</el-button>
                </el-form-item>
              </el-form>
            </transition>

          </div>

        </div>
        <div class="list">
          <el-table row-class-name="row-expand-cover" :data="tableData5" row-key="id" :expand-row-keys="expands"
            @row-click="rowClick" style="width: 100%;border-radius: 5px;">
            <el-table-column type="expand" style="display: flex;">
              <template slot-scope="props" style="display: flex;">
                <div class="props">
                  <div class="propsA">
                    <h4 class="detail-title">任务信息</h4>

                    <el-form label-position="left" inline class="demo-table-expand">
                      <div style="display: flex;">
                        <ul>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">任务编号</div>
                              <span>{{ props.row.name }}</span>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">任务类型</div>
                              <span>{{ props.row.shop }}</span>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">状态</div>
                              <span>{{ props.row.id }}</span>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">执行人</div>
                              <span>{{ props.row.shopId }}</span>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">创建时间</div>
                              <span>{{ props.row.category }}</span>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">截止时间</div>
                              <span>{{ props.row.address }}</span>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">紧急程度</div>
                              <span>{{ props.row.desc }}</span>
                            </el-form-item>
                          </li>
                        </ul>
                        <ul>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">截止时间</div>
                              <ul class="wenjian" style="width: 188px;">
                                <li v-for="item in fileList">
                                  <div>
                                    <i class="el-icon-document"></i>
                                    <span>{{item.name}}</span>
                                  </div>

                                  <i class="el-icon-download"></i>
                                </li>
                              </ul>
                            </el-form-item>
                          </li>
                          <li>
                            <el-form-item lable-position="right" label-width="70px">
                              <div slot="label" style="text-align:right;color: #ABB8C9;">紧急程度</div>
                              <span>{{ props.row.desc }}</span>
                            </el-form-item>
                          </li>

                        </ul>
                      </div>








                    </el-form>

                  </div>
                  <div class="propsB">
                    <h3 class="detail-title">任务反馈</h3>
                    <el-form label-position="left" inline class="demo-table-expand">
                      <div style="display: flex;    flex-wrap: wrap;">

                        <el-form-item style="margin-top: 28px;" lable-position="right" label-width="70px">
                          <div slot="label" style="text-align:right;color: #ABB8C9;">任务编号</div>
                          <textarea></textarea>
                        </el-form-item>
                        <el-form-item style="margin-top: 20px;margin-left: 70px;">
                          <el-button size="mini">取消</el-button>
                          <el-button size="mini" type="primary">提交</el-button>
                        </el-form-item>

                      </div>








                    </el-form>
                  </div>
                </div>




              </template>

            </el-table-column>
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column label="任务名称" prop="id">
            </el-table-column>
            <el-table-column label="任务编号" prop="name">
            </el-table-column>
            <el-table-column label="任务类型" prop="desc">
            </el-table-column>
            <el-table-column label="执行人" prop="desc">
            </el-table-column>
            <el-table-column label="状态" prop="desc">
              <template slot-scope="props">
                <el-button v-if="props.row.type ==0" size="mini" type="primary" plain>已完成</el-button>
                <el-button v-if="props.row.type ==1" size="mini" type="primary" plain>已发布</el-button>
                <el-button v-if="props.row.type ==2" size="mini" type="success" plain>执行中</el-button>
                <el-button v-if="props.row.type ==3" size="mini" type="warning" plain>待评价</el-button>
              </template>
            </el-table-column>
            <el-table-column label="创建时间" prop="desc">
            </el-table-column>
            <el-table-column label="操作" prop="desc">
              <template slot-scope="props">
                <el-button v-if="props.row.type ==0" size="mini" type="primary">评价</el-button>
                <el-button v-if="props.row.type ==1" size="mini" type="primary">接收</el-button>
                <el-button v-if="props.row.type ==2" size="mini" type="primary">反馈</el-button>
                <el-button v-if="props.row.type ==3" size="mini">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination class="fenye" @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper"
            :total="400">
          </el-pagination>
        </div>
      </el-main>

    </el-container>
    <el-footer style="height: 30px;font-size: 12px;line-height: 30px;">2019 @So-well</el-footer>
  </el-container>
</template>

<script>
  import index from './detail.js'
  export default index
</script>
<style lang="less" scoped>
  @import "./detail.less";
</style>
<style scoped>
  .row-expand-cover {
    color: red;

  }
</style>



