import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Test from '@/pages/test/index.vue'; //首页
import Detail from '@/pages/detail/detail.vue'; //内容页
import Newdemo from '@/pages/newdemo/index.vue'; //新点子
import Test2 from '@/pages/test2/index.vue'; //新点子
import Test3 from '@/pages/test3/index.vue'; //新点子
import City from '@/pages/test3/city.vue'; //新点子
import Area from '@/pages/test3/area.vue'; //新点子
import Test4 from '@/pages/test4/index.vue'; //新点子
import zhejiang3D from '@/components/module/3Dzhejiang/zhejiang.vue'; //练习
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/Test',
      name: 'Test',
      component: Test
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/Newdemo',
      name: 'Newdemo',
      component: Newdemo
    },
    {
      path: '/Test2',
      name: 'Test2',
      component: Test2
    },
    {
      path: '/Test3',
      name: 'Test3',
      component: Test3
    },
    {
      path: '/Area',
      name: 'Area',
      component: Area
    },
    {
      path: '/City',
      name: 'City',
      component: City
    },
    {
      path: '/Test4',
      name: 'Test4',
      component: Test4
    },
    {
      path: '/zhejiang3D',
      name: 'zhejiang3D',
      component: zhejiang3D
    }
  ]
})



